.loginForm {
  display: flex;
  flex-direction: column;
  width: 250px;
  margin: 20px auto;
  background-color: white;
  padding: 5vw;
  box-shadow: 2px 2px rgb(220, 244, 245);
  border-radius: 10px;
  align-items: center;
}
.btnLogin {
  width: fit-content;
  margin: auto;
  padding: 5px 20px;
  border: none;
  background-color: blue;
  color: white;
  border-radius: 10px;
  font-size: 20px;
}
.loginForm input {
  padding: 10px 0;
  /* margin: 20px auto; */
}
body {
  background-color: rgb(245, 245, 245);
}
.loginForm div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
#LinearProgress {
  display: none;
}
.help {
  cursor: pointer;
  margin-top: 10px;
}
.signupRegister {
  text-decoration: none;
  margin-top: 10px;
}
